export default{
  data() {
    return {
      user_coupon_id: '',              // 用户优惠券ID
      detail: '',
      url: '',
      params: '',
      isSure: false
    }
  },
  onLoad(opt) {
    let that= this
    if(opt.params) {
      that.params= JSON.parse(opt.params)
      that.getDetail(that.params)  
    }
    that.getDetail(that.params) 
  },
  methods: {
    
    getDetail(params) {
      let that= this
      that.$httpRequest({
        url: that.$api.userCouponDetails,
        data: {
          user_coupon_id: params.user_coupon_id
        }
      }).then(res => {
        console.log(res.data.detail)
        that.detail= res.data.detail
      })
    },
    
    // 确认使用
    sureUse() {
      uni.showLoading({
        title: '兑换中'
      })
      let that= this
      if(!that.isSure) {
        that.isSure= true
        that.$httpRequest({
          url: that.$api.confirmUseCoupon,
          data: {
            user_coupon_id: that.params.user_coupon_id
          }
        }).then(res => {
          uni.hideLoading()
          uni.navigateBack({
            delta: 1,
            success: function() {
              uni.showToast({
                title: '兑换成功'
              })
            }
          })
        })
        setTimeout(function() {
          that.isSure
        }, 500)
      }
    }
    
  }
}
